e}) => ({id, text: name})),\n            pagination: {\n              more: data.current_page < data.last_page\n            }\n          };\n        }\n      }\n    },\n\n    settings() {\n      return {\n        multiple: 'true',\n        tags: this.tags,\n        theme: 'bootstrap4',\n        ajax: this.url ? this.ajax : null,\n      }\n    },\n  },\n\n\n  watch: {\n    value() {\n      this.selects = this.value\n    }\n  },\n\n  methods: {\n    onChange(e) {\n      this.$emit('input', e)\n    },\n  },\n}\n</script>\n\n<style scoped>\n.height-auto {\n  height: auto !important;\n}\n</style>\n"],"sourceRoot":""}]);
