___CSS_LOADER_EXPORT___.push([module.id, ".card-body[data-v-f070ae28]{flex:1 1 auto;min-height:1px;padding:0}", "",{"version":3,"sources":["webpack://./resources/js/components/Reports/ReportByCounty.vue"],"names":[],"mappings":"AA8JA,4BACA,aAAA,CACA,cAAA,CACA,SACA","sourcesContent":["<template>\n  <div class=\"col-md-8\">\n    <div class=\"card border shadow-sm\">\n      <div class=\"card-header shadow-none bg-light rounded d-flex justify-content-between\">\n        <h4 class=\"mt-1 ml-2 page-title\">Number of Sites per County</h4>\n        <input type=\"search\" class=\"form-control\" placeholder=\"Search...\" v-model=\"search\">\n      </div>\n      <inline-spinner class=\"ml-5 mt-5 mb-5\" v-if=\"loading\"/>\n      <div class=\"card-body\" v-if=\"!loading\">\n        <span v-if=\"counties.length === 0\"> No sites found</span>\n        <table class=\"table table-middle table-bordered mb-0\" v-if=\"counties.length > 0\">\n          <thead>\n          <tr>\n            <th>\n              <a href=\"#\" class=\"d-flex align-items-center\" @click.prevent=\"sort('name')\">\n                <span>{{ 'State' }}</span>\n                <icon :name=\"sortIcon('name')\"\n                      class=\"ml-2\"\n                      :class=\"{'invisible': orderBy !== 'name'}\"\n                      style=\"width: 20px;\"/>\n              </a>\n            </th>\n            <th>\n              <a href=\"#\" class=\"d-flex align-items-center\" @click.prevent=\"sort('sites_count')\">\n                <span>{{ 'Total' }}</span>\n                <icon :name=\"sortIcon('sites_count')\"\n                      class=\"ml-2\"\n                      :class=\"{'invisible': orderBy !== 'sites_count'}\"\n                      style=\"width: 20px;\"/>\n              </a>\n            </th>\n          </tr>\n          </thead>\n          <tbody>\n          <tr v-for=\"county in counties\" v-if=\"county.sites_count > 0\">\n            <td>\n              {{ county.name }}\n            </td>\n            <td>\n              {{ county.sites_count }}\n            </td>\n          </tr>\n          </tbody>\n        </table>\n      </div>\n    </div>\n    <pager class=\"mt-2\" :last-page=\"lastPage\" :page=\"page\" @change=\"goTo($event)\"/>\n  </div>\n</template>\n\n<script>\nimport TokensField from \"../TokensField\";\nimport InlineSpinner from \"../InlineSpinner\";\nimport Dropdown from \"../Dropdown\";\nimport Icon from \"../Icon\";\nimport User from \"../../helpers/User\";\nimport ApexChart from \"vue-apexcharts\";\nimport Errors from \"../../forms/Errors\";\nimport Pager from \"../Pager\";\n\nexport default {\n  name: 'ReportByCounty',\n\n  components: {ApexChart, InlineSpinner, TokensField, Dropdown, Icon, Pager},\n\n  data() {\n    return {\n      User,\n      loading: true,\n      page: 1,\n      lastPage: 1,\n      limit: 10,\n      search: '',\n      orderBy: 'name',\n      orderDir: 'asc',\n      counties: null,\n      _request: null,\n    }\n  },\n\n  mounted() {\n    this.loadReport()\n  },\n\n  watch: {\n    search() {\n      this.page = 1\n      this.loadReport()\n    },\n  },\n\n  methods: {\n    async loadReport() {\n      if (this._request) {\n        this._request()\n      }\n\n      this.loading = true\n\n      try {\n        const {data} = await axios.get(`/web/reports/site-county`, {\n          params: {\n            page: this.page,\n            order_by: this.orderBy,\n            order_dir: this.orderDir,\n            search: this.search,\n            limit: this.limit,\n          },\n          cancelToken: new axios.CancelToken(c => this._request = c),\n        })\n        this.total = data.total\n        this.counties = data.data\n        this.lastPage = data.last_page\n      } catch (e) {\n        if (!axios.isCancel()) {\n          if (e.response && e.response.status === 422) {\n            const errors = new Errors(e.response.data)\n            this.$alert(errors.toArray().join(' '))\n          } else {\n            this.$alert('Unable to load sites. Please try refreshing the page or contact us.')\n          }\n          console.error(e)\n        }\n      }\n      this.loading = false\n    },\n\n    goTo(page) {\n      this.page = page\n      this.loadReport()\n    },\n\n    sort(field) {\n      if (field === this.orderBy) {\n        this.orderDir = this.orderDir === 'asc' ? 'desc' : 'asc'\n      } else {\n        this.orderBy = field\n        this.orderDir = 'asc'\n      }\n      this.loadReport()\n    },\n\n    sortIcon(field) {\n      if (field === this.orderBy) {\n        if (this.orderDir === 'asc') {\n          return 'arrow-up'\n        } else {\n          return 'arrow-down'\n        }\n      } else {\n        return ''\n      }\n    },\n  },\n}\n</script>\n\n<style scoped>\n.card-body {\n  flex: 1 1 auto;\n  min-height: 1px;\n  padding: 0rem;\n}\n</style>\n"],"sourceRoot":""}]);
